.module-wrapper {
  background-color: #dad2f2;
  border-top: solid 1px #cccccc;
  padding: 12px 10px;
}

.module-lead {
  text-align: center;
  width: 80px;
  background: #fa8e04;
  color: white;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 1rem;
  letter-spacing: 1px;
  line-height: 16px;
  font-weight: 500;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.module-title {
  margin-bottom: 1rem;
  color: #444444;
  font-size: 1.1rem;
}

.module-link {
  display: flex;
  align-items: center;
  font-weight: bold;
}

.module-label {
  margin-right: 0.5rem;
}
